import ajax from "../../util/ajax.js"
export default {
	data() {
		return {
			goodsData:[]
		}
	},
	async mounted() {
		let {goodId} = this.$mp.query
		// console.log(goodId)
		let result = await ajax("/getgooddetail",{goodId})
		this.goodsData = result
	},
	methods:{
		addCart(){
			this.$store.commit("addShopCart",this.goodsData)
		}
		
	}
}
